import axios from 'axios';

// Definir los tipos de datos para nuestras peticiones y respuestas
export interface TicketCreationData {
  name: string;
  email: string;
  message: string;
  metadata: {
    url: string;
    referrer: string;
    userAgent: string;
    screenSize: string;
  };
}

export interface TicketCreationResponse {
  success: boolean;
  ticketId: string;
  liveChatAvailable: boolean;
}

export interface MessageData {
  ticketId: string;
  message: string;
}

export interface MessageResponse {
  success: boolean;
  messageId: string;
  timestamp: string;
}

export interface WidgetConfig {
  apiUrl: string;
  widgetId: string;
  widgetToken: string;
}

// Crear una instancia personalizada de la API
const createApiClient = (config: WidgetConfig) => {
  console.log('Creando cliente API con configuraciÃ³n:', { 
    baseURL: config.apiUrl,
    widgetId: config.widgetId,
    hasToken: !!config.widgetToken 
  });
  
  const apiClient = axios.create({
    baseURL: config.apiUrl,
    headers: {
      'Content-Type': 'application/json',
      'X-Widget-ID': config.widgetId,
      'X-Widget-Token': config.widgetToken
    },
    timeout: 10000, // Aumentar timeout a 10 segundos
    withCredentials: false // Asegurarnos que no se envÃ­an credenciales cross-domain
  });

  // Agregar interceptores para manejo de errores
  apiClient.interceptors.request.use(
    config => {
      console.log(`Enviando solicitud a ${config.url}`, config);
      return config;
    },
    error => {
      console.error('Error en la solicitud:', error);
      return Promise.reject(error);
    }
  );

  apiClient.interceptors.response.use(
    response => {
      console.log(`Respuesta recibida de ${response.config.url}:`, response.status, response.data);
      return response;
    },
    error => {
      console.error('Error en la comunicaciÃ³n con la API del widget:', error.message, error.response?.status, error.response?.data);
      return Promise.reject(error);
    }
  );

  return apiClient;
};

// Variable para almacenar la configuraciÃ³n actual
let currentConfig: WidgetConfig = {
  apiUrl: 'https://api.growdesk.com/widget',
  widgetId: '',
  widgetToken: ''
};

// FunciÃ³n para inicializar o actualizar la configuraciÃ³n
export const configureWidgetApi = (config: Partial<WidgetConfig>) => {
  currentConfig = { ...currentConfig, ...config };
  return currentConfig;
};

// Hook para usar la API del widget
export const useWidgetApi = () => {
  const apiClient = createApiClient(currentConfig);

  // Crear un nuevo ticket
  const createTicket = async (data: TicketCreationData): Promise<TicketCreationResponse> => {
    try {
      const response = await apiClient.post('/widget/tickets', data);
      return response.data;
    } catch (error) {
      console.error('Error al crear el ticket:', error);
      throw new Error('No pudimos crear tu ticket. Por favor, intÃ©ntalo de nuevo mÃ¡s tarde.');
    }
  };

  // Enviar un mensaje en una conversaciÃ³n existente
  const sendMessage = async (data: MessageData): Promise<MessageResponse> => {
    try {
      const response = await apiClient.post('/widget/messages', data);
      return response.data;
    } catch (error) {
      console.error('Error al enviar el mensaje:', error);
      throw new Error('No pudimos enviar tu mensaje. Por favor, intÃ©ntalo de nuevo mÃ¡s tarde.');
    }
  };

  // Obtener el historial de mensajes de un ticket
  const getMessageHistory = async (ticketId: string) => {
    try {
      const response = await apiClient.get(`/widget/tickets/${ticketId}/messages`);
      return response.data;
    } catch (error) {
      console.error('Error al obtener el historial de mensajes:', error);
      throw new Error('No pudimos cargar tus mensajes anteriores.');
    }
  };

  // Subir un archivo adjunto
  const uploadAttachment = async (ticketId: string, file: File) => {
    try {
      const formData = new FormData();
      formData.append('file', file);
      formData.append('ticketId', ticketId);
      
      const response = await apiClient.post('/attachments', formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      });
      
      return response.data;
    } catch (error) {
      console.error('Error al subir el archivo:', error);
      throw new Error('No pudimos subir tu archivo. Por favor, intÃ©ntalo de nuevo.');
    }
  };

  // Verificar el estado del servidor
  const checkServerStatus = async () => {
    try {
      const response = await apiClient.get('/widget/status');
      return response.data.online;
    } catch (error) {
      console.error('Error al verificar el estado del servidor:', error);
      return false;
    }
  };

  return {
    createTicket,
    sendMessage,
    getMessageHistory,
    uploadAttachment,
    checkServerStatus
  };
};

// Exportar una funciÃ³n para inicializar la API con valores obtenidos del script de carga
export const initializeFromScript = () => {
  console.log('Initializing widget API from script');
  
  // Obtener el script actual
  const scriptTag = document.querySelector('script[src*="widget.js"]') || 
                   document.getElementById('growdesk-widget') ||
                   document.currentScript as HTMLScriptElement;
  
  if (scriptTag) {
    console.log('Found widget script tag', scriptTag);
    
    const widgetId = scriptTag.getAttribute('data-widget-id');
    const apiUrl = scriptTag.getAttribute('data-api-url');
    const widgetToken = scriptTag.getAttribute('data-widget-token');
    
    console.log('Widget initialization parameters:', { widgetId, apiUrl, widgetToken: widgetToken ? '***' : null });
    
    if (widgetId) {
      const config: Partial<WidgetConfig> = { widgetId };
      
      if (apiUrl) config.apiUrl = apiUrl;
      if (widgetToken) config.widgetToken = widgetToken;
      
      const finalConfig = configureWidgetApi(config);
      console.log('Widget API configured successfully', { 
        apiUrl: finalConfig.apiUrl,
        widgetId: finalConfig.widgetId,
        hasToken: !!finalConfig.widgetToken 
      });
    } else {
      console.error('GrowDesk Widget: No se proporcionÃ³ un ID de widget');
    }
  } else {
    console.error('GrowDesk Widget: No se pudo encontrar el script del widget');
  }
}; 